import React from 'react';
import { Col, Container, Row } from "react-bootstrap";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import meter1 from '../assets/img/ho1.jpg';
import meter2 from '../assets/img/ho2.jpg';
import meter3 from '../assets/img/ho3.jpg';
import meter4 from '../assets/img/ho4.jpg';
import meter5 from '../assets/img/ho5.png';
export const Pagdos = () => {

    const responsive = {
        superLargeDesktop: {
          // the naming can be any, depends on you.
          breakpoint: { max: 4000, min: 3000 },
          items: 5
        },
        desktop: {
          breakpoint: { max: 3000, min: 1024 },
          items: 3
        },
        tablet: {
          breakpoint: { max: 1024, min: 464 },
          items: 2
        },
        mobile: {
          breakpoint: { max: 464, min: 0 },
          items: 1
        }
      };

  return (
    <Container>
        <Row>
            <Col>
                <div className="skill-bx"> 
                    <center><h2>Productos para tu Cocina</h2></center>
                    <Carousel responsive={responsive} infinite={true} >
                        <div className='cocina'>
                            <img src={meter1} alt="image"></img>
                            <p> </p>
                            <h2>Cecotec Robot de Cocina Multifuncin</h2>
                        </div>
                        <div className='cocina'>
                            <img src={meter2} alt="image"></img>
                            <h2>Caldera Electrica</h2>
                        </div>
                        <div className='cocina'>
                            <img src={meter3} alt="image"></img>
                            <h2>Freidora de Aire</h2>
                        </div>
                        <div className='cocina'>
                            <img src={meter4} alt="image"></img>
                            <h2>Refrigerador inteligente</h2>
                        </div>
                        <div className='cocina'>
                            <img src={meter5} alt="image"></img>
                            <h2>Licuadora Oster Pro</h2>
                        </div>
                    </Carousel>
                </div>
            </Col>
        </Row>
    </Container>

  )
}
