import React from 'react'
import { Col, Container, Row } from "react-bootstrap";
import jue1 from '../assets/img/ju1.jpg';
import jue2 from '../assets/img/ju2.jpg';
import jue3 from '../assets/img/ju3.jpg';
import jue4 from '../assets/img/ju4.jpeg';


export const Pagtres = () => {
  return (
    <Container>
            <section className="juegos" id="home">
                <center><h2>Consolas de VideoJuegos</h2></center>
                <Row className="align-items-center">
                    <Col xs={12} md={6} xl={7}>
                        <h2>Play Station 5</h2>
                        <p>Mejora la capacidad de almacenamiento de tu consola PS5 con una unidad de estado slido M. 2 compatible. Ahora puedes jugar tus juegos y aplicaciones de medios de PS5 y PS4 directamente desde la expansin de SSD de alta velocidad, la cual tambin puedes utilizar para almacenar, descargar y administrar este contenido.</p>

                    </Col>
                    <Col  xs={12} md={6} xl={5}>
                        <img src={jue1} alt="Header" />
                    </Col>
                </Row>
                <Row className="align-items-center">
                    <Col xs={12} md={6} xl={7}>
                        <h2>Nintendo Switch</h2>
                        <p>Obtendrs acceso a ms de 100 juegos de Super NES y NES, juego en lnea en algunos de tus juegos favoritos, guardado de datos en la nube en juegos compatibles y ms.</p>

                    </Col>
                    <Col  xs={12} md={6} xl={5}>
                        <img src={jue2} alt="Header" />
                    </Col>
                </Row>
                <Row className="align-items-center">
                    <Col xs={12} md={6} xl={7}>
                        <h2>Nintendo Wii</h2>
                        <p>La capacidad de almacenamiento mxima admitida por Wii U es de 2 TB (terabytes). La consola Wii U reconocer los dispositivos de almacenamiento con capacidades superiores a 2 TB, pero la capacidad mxima de almacenamiento seguir siendo de 2 TB.</p>

                    </Col>
                    <Col  xs={12} md={6} xl={5}>
                        <img src={jue3} alt="Header" />
                    </Col>
                </Row>
                <Row className="align-items-center">
                    <Col xs={12} md={6} xl={7}>
                        <h2>Play Station Portal</h2>
                        <p>PlayStation Portal Remote Player es un dispositivo diseado especficamente para brindar una increble experiencia de juego remoto. Eso significa que puedes encenderlo, presionar un botn para conectarlo a tu consola PS5 y comenzar a jugar a una velocidad notable</p>

                    </Col>
                    <Col  xs={12} md={6} xl={5}>
                        <img src={jue4} alt="Header" />
                    </Col>
                </Row>
            
        </section>

            </Container>
  )
}
