import { Col, Container, Row } from "react-bootstrap";


import logo from '../assets/img/logo.jpeg';
import { ArrowRightCircle } from "react-bootstrap-icons";

export const PagUno = () =>{


      return(
        <section className="skills" id="link">
            <Container>
            <section className="baner" id="home">
            
                <Row className="align-items-center">
                    <Col xs={12} md={6} xl={7}>
                        <center><h1>Bienvenido a Digital Store</h1></center>
                        <p>En la Tienda encontraras todo lo relacionado con Gaming como Monitores, Mouse y Audfonos. Gran Variedad de Productos Electrnicos como Laptops, Smartphones y nucho mas. Tambien podras encontrar todo lo necesario para equipar tu hogar.</p>
                        
                    </Col>
                    <Col  xs={12} md={6} xl={5}>
                        <img src={logo} alt="Header" />
                    </Col>
                </Row>
            
        </section>

            </Container>
        </section>
      )
}