import React from 'react'
import { Container } from 'react-bootstrap'
import ReactPlayer from 'react-player'
import video from '../assets/video/novedades.mp4'

export const Novedad = () => {
  return (
    <Container>
        <div className='novedad'>
            <center><h2>Ultimas Novedades</h2></center>
            <ReactPlayer url={video} controls loop width='100%' height='100%'></ReactPlayer>

        </div>

    </Container>
  )
}
