import React from 'react'

import cel1 from '../assets/img/cel1.jpg';
import cel2 from '../assets/img/cel2.jpg';
import cel3 from '../assets/img/cel3.jpg';
import com1 from '../assets/img/comp1.png';
import com2 from '../assets/img/comp2.jpg';
import com3 from '../assets/img/comp3.jpg';
import rel from '../assets/img/rel.png';
import rel2 from '../assets/img/rel2.jpeg';
import aud from '../assets/img/aud.jpg';

import { Container } from 'react-bootstrap';

export const Pagcin = () => {
  return (
    <Container>
        <div className='cinco'>
            <ul className="nav nav-tabs" id="myTab" role="tablist">
            <li className="nav-item" role="presentation">
                <button class="nav-link active" id="home-tab" data-bs-toggle="tab" data-bs-target="#home-tab-pane" type="button" role="tab" aria-controls="home-tab-pane" aria-selected="true">Smartphones</button>
            </li>
            <li className="nav-item" role="presentation">
                <button className="nav-link" id="profile-tab" data-bs-toggle="tab" data-bs-target="#profile-tab-pane" type="button" role="tab" aria-controls="profile-tab-pane" aria-selected="false">Computadoras</button>
            </li>
            <li className="nav-item" role="presentation">
                <button className="nav-link" id="contact-tab" data-bs-toggle="tab" data-bs-target="#contact-tab-pane" type="button" role="tab" aria-controls="contact-tab-pane" aria-selected="false">Reloj Digital</button>
            </li>
            <li className="nav-item" role="presentation">
                <button className="nav-link" id="disabled-tab" data-bs-toggle="tab" data-bs-target="#disabled-tab-pane" type="button" role="tab" aria-controls="disabled-tab-pane" aria-selected="false">Audifonos</button>
            </li>
            </ul>
            <div className="tab-content" id="myTabContent">
                <div className="tab-pane fade show active" id="home-tab-pane" role="tabpanel" aria-labelledby="home-tab" tabindex="0">
                    <center><h2>Smartphones</h2></center>
                    <div className="card" style={{width: '18rem'}}>
                        <img src={cel1} class="card-img-top" />
                        <div className="card-body">
                        <p className="card-text">
                        <h5>Celular Samsung - A73 5G, 256GB 8 RAM - Plomo</h5>
                        </p>
                        </div>
                    </div>
                    <div className="card" style={{width: '18rem'}}>
                        <img src={cel2} class="card-img-top" />
                        <div className="card-body">
                        <p className="card-text">
                        <h5>Apple - iPhone 11, versin de EE.UU., 64GB - Negro</h5>
                        </p>
                        </div>
                    </div>
                    <div className="card" style={{width: '18rem'}}>
                        <img src={cel3} class="card-img-top" />
                        <div className="card-body">
                        <p className="card-text">
                        <h5>Celular Realme 8 6,5" 128GB 4GB 48MP 16MP - Celeste</h5>
                        </p>
                        </div>
                    </div>
                </div>
                <div className="tab-pane fade" id="profile-tab-pane" role="tabpanel" aria-labelledby="profile-tab" tabindex="0">
                    <center><h2>Articulos para computadoras</h2></center>
                        <div className="card" style={{width: '18rem'}}>
                            <img src={com1} class="card-img-top" />
                            <div className="card-body">
                            <p className="card-text">
                            <h5>Laptop gaming HP Victus serie 15-fb0000 de 15,6 pulgadaso</h5>
                            </p>
                            </div>
                        </div>
                        <div className="card" style={{width: '18rem'}}>
                            <img src={com2} class="card-img-top" />
                            <div className="card-body">
                            <p className="card-text">
                            <h5>Teclado Gamer con RGB 4JK54</h5> 
                            </p>
                            </div>
                        </div>
                        <div className="card" style={{width: '18rem'}}>
                            <img src={com3} class="card-img-top" />
                            <div className="card-body">
                            <p className="card-text">
                            <h5>MOUSE GAMER KIRA (JL910)</h5>
                            </p>
                            </div>
                        </div>
                </div>
                <div className="tab-pane fade" id="contact-tab-pane" role="tabpanel" aria-labelledby="contact-tab" tabindex="0">
                        <center><h2>Reloj Inteligente</h2></center>
                        <div className="card" style={{width: '18rem'}}>
                            <img src={rel} class="card-img-top" />
                            <div className="card-body">
                            <p className="card-text">
                            <h5>RELOJ INTELIGENTE TOR 5K - GMD</h5>
                            </p>
                            </div>
                        </div>
                        <div className="card" style={{width: '18rem'}}>
                            <img src={rel2} class="card-img-top" />
                            <div className="card-body">
                            <p className="card-text">
                            <h5>RELOJ INTELIGENTE M7 - PROMART</h5>
                            </p>
                            </div>
                        </div>
                </div>
                <div className="tab-pane fade" id="disabled-tab-pane" role="tabpanel" aria-labelledby="disabled-tab" tabindex="0">
                    <center><h2>Audifonos Gamer</h2></center>
                        <div className="card" style={{width: '18rem'}}>
                            <img src={aud} class="card-img-top" />
                            <div className="card-body">
                            <p className="card-text">
                            <h5>Audifonos Gamer G9000 Kotion Each Con Luces Led Play Pc Xbox KOTION</h5>
                            </p>
                            </div>
                        </div>
                </div>
            </div>

        </div>

    </Container>
  )
}
